<template>
  <div>
    <da-manage-page>
      <div slot="button">
        <el-button icon="my-zengjia" @click="dialogVisible=true;">新增</el-button>
      </div>
      <el-form slot="search" :inline="true" :model="searchForm.formdata">
        <el-form-item label="企业能源">
          <!-- <el-input v-model="searchForm.formdata.entEnergyId"></el-input> -->
          <el-select v-model="searchForm.formdata.entEnergyId" filterable placeholder="请选择">
            <el-option label="全部" value=""></el-option>
            <el-option v-for="item in entEnergyIdOptions" :key="item.dataValue" :label="item.dataText" :value="item.dataValue"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="生效时间" prop="effectiveTime">
          <el-date-picker v-model="searchForm.formdata.effectiveTime" type="date" placeholder="选择日期"></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getTbList">查询</el-button>
        </el-form-item>
      </el-form>
      <da-table ref="table" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

        <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
            <el-button size="small" @click="delTb(scope.row)">删除</el-button>
          </template>
        </el-table-column>

      </da-table>
    </da-manage-page>

    <da-dialog :title="title" :visible.sync="dialogVisible" @close="closeDialog('addForm')">
      <el-form label-width="120px" :model="addForm" :rules="rules" ref="addForm">

        <el-form-item label="企业能源" prop="entEnergyId">
          <el-select v-model="addForm.entEnergyId" filterable placeholder="请选择">
            <el-option v-for="item in entEnergyIdOptions" :key="item.dataValue" :label="item.dataText" :value="item.dataValue"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="等价热值" prop="dengValue">
          <el-input auto-complete="off" v-model="addForm.dengValue"></el-input>
        </el-form-item>
        <el-form-item label="当量热值" prop="dangValue">
          <el-input auto-complete="off" v-model="addForm.dangValue"></el-input>
        </el-form-item>
        <el-form-item label="计量单位" prop="displayUnit">
          <el-radio-group v-model="addForm.displayUnit">
            <el-radio v-for="item in displayUnitOptions" :key="item.dataValue" :label="item.dataText" :value="item.dataValue"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="等价折标系数" prop="dengModulus">
          <el-input auto-complete="off" v-model="addForm.dengModulus"></el-input>
        </el-form-item>
        <el-form-item label="当量折标系数" prop="dangModulus">
          <el-input auto-complete="off" v-model="addForm.dangModulus"></el-input>
        </el-form-item>
        <el-form-item label="生效时间" prop="effectiveTime">
          <el-date-picker v-model="addForm.effectiveTime" type="date" placeholder="选择日期"></el-date-picker>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
  </div>
</template>
<script>
export default {
  name: "calorificEnergy",
  data() {
    return {
      entEnergyIdOptions: [],
      displayUnitOptions: [
        { dataText: "KJ", dataValue: "KJ" },
        { dataText: "KCal", dataValue: "KCal" }
      ],
      // 查询表单
      searchForm: {
        formdata: { entEnergyId: "", effectiveTime: "" },
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头

        {
          label: "企业能源",
          prop: "entEnergyId",
          align: "center",
          sortable: true,
          displace: {}
        },
        {
          label: "等价热值",
          prop: "dengValue",
          align: "center",
          sortable: true
        },
        {
          label: "当量热值",
          prop: "dangValue",
          align: "center",
          sortable: true
        },
        {
          label: "计量单位",
          prop: "displayUnit",
          align: "center",
          sortable: true
        },
        {
          label: "等价折标系数",
          prop: "dengModulus",
          align: "center",
          sortable: true
        },
        {
          label: "当量折标系数",
          prop: "dangModulus",
          align: "center",
          sortable: true
        },
        {
          label: "生效时间",
          prop: "effectiveTime",
          align: "center",
          format: "YYYY-MM-DD",
          sortable: true
        }
      ],
      dialogVisible: false,
      addForm: {
        entEnergyId: "",
        dengValue: "",
        dangValue: "",
        displayUnit: "",
        dengModulus: "",
        dangModulus: "",
        effectiveTime: ""
      },
      rules: this.eachValidate({
        entEnergyId: { required: true },
        dengValue: {},
        dangValue: {},
        displayUnit: { maxString: "5" },
        dengModulus: {},
        dangModulus: {},
        effectiveTime: { required: true }
      })
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑能源质量管理" : "新增能源质量管理";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getTbList() {
      let formName = "table";
      this.searchForm.formdata.effectiveTime =
        this.searchForm.formdata.effectiveTime || "";
      this.$ajax
        .get("rest/calorificEnergy/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    getEntenergyIdInfo() {
      this.$ajax.get("rest/calorificEnergy/entenergyId/get").then(res => {
        if (res.data.status) {
          this.entEnergyIdOptions = res.data.records;
          let toObj = {};
          this.$each(res.data.records, item => {
            toObj[item.dataValue] = item.dataText;
          });
          this.colsData[0].displace = toObj;
        }
      });
    },
    editTb(row) {
      this.dialogVisible = true;
      this.addForm = JSON.parse(JSON.stringify(row));
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = {
          entEnergyId: "",
          dengValue: "",
          dangValue: "",
          displayUnit: "",
          dengModulus: "",
          dangModulus: "",
          effectiveTime: ""
        };
      });
    },
    saveDialog(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/calorificEnergy", this.addForm).then(
            res => {
              if (res.data.status) {
                this.$message.success(res.data.answerMsg);
                this.dialogVisible = false;
                this.getTbList();
              } else {
                this.$message.error(res.data.answerMsg);
              }
            }
          );
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/calorificEnergy", { params: { id: row.id } })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    }
  },
  mounted() {
    this.getEntenergyIdInfo();
    this.getTbList();
  }
};
</script>



